<template>
  <div class="UserInfo">
    <div class="loadingList">
      <img src="../assets/loadingimg.gif" v-if="isLoading" alt="">
    </div>
    <div class="userInfomation">
      <section>
        <img :src="userinfo.avatar_url" alt="">
        <span>{{userinfo.loginname}}</span>
        <p>
          {{userinfo.score}}积分
        </p>
        <p>
          注册事件：{{userinfo.create_at | formatDate}}
        </p>
      </section>
      <div class="replies">
        <p>回复的主题</p>
        <ul>
          <li v-for="item in userinfo.recent_replies">
            <router-link :to="{ name:'post_count',params:{id:item.id  }  }">
              {{item.title}}
            </router-link>
          </li>
        </ul>
      </div>
      <div class="topics">
        <p>创建的主题</p>
        <ul>
          <li v-for="item in userinfo.recent_topics">
            <router-link :to="{ name:'post_count',params:{id:item.id  }  }">
              {{item.title}}
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isLoading:false,
      userinfo:{}
    }
  },
  computed: {},
  components: {},
  created() {},
  methods: {
    getData(){
      this.$http.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
          .then(res=>{
        this.isLoading=false //加载成功 去除动画
        this.userinfo =res.data.data
      }).catch(err=>{
        //处理返回后的问题
        console.log(err)
      })
    }
  },
  beforeMount() {
    //加载成功之前显示加载动画
    this.isLoading=true
    this.getData()//在页面加载之前获取数据
  }

}
</script>

<style scoped>
.userInfomation {
  background: white;
  width: 75%;
  margin: 10px auto;
}
.userInfomation section {
  padding: 12px;
}
.userInfomation img {
  width: 30px;
}
.userInfomation li {
  list-style:none;
}
.userInfomation .replies,
.userInfomation .topics {
  font-size: 0.72rem;
  border-top: 10px #DDDDDD solid;
}
.userInfomation > div > p {
  padding: 12px 0 12px 12px;
  background-color: rgba(212, 205, 205, 0.17);
  font-size: 0.75rem;
  margin: 0;
}
.userInfomation > div >ul > li {
  padding: 4px 0 4px 12px;
  white-space: nowrap;
  font-size: 0.72rem;
  text-overflow: ellipsis;
  overflow: hidden;
  line-height: 30px;
  vertical-align: middle;
}
.userInfomation > div >ul > li > a {
  color: #094E99;
  text-decoration: none;
}
</style>